import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

// components
import { Container } from '../../components/ExternalContainer/styles';
// styles
import { PhoneBackground, Logo, SimpleText, ProgressBar } from './styles';

const Loading = () => {
  const { push } = useHistory();

  useEffect(() => {
    setTimeout(() => push('/conectado'), 5500);
  }, [push]);

  return (
    <Container>
      <PhoneBackground>
        <Logo />

        <ProgressBar />

        <SimpleText color={'#4767bd'}>Conectando...</SimpleText>

        <div style={{ marginTop: '30px' }}></div>

        <SimpleText color={'#ffffff'}>
          Carregando conversas, contatos secretos, redes sociais e aplicativos
          do celular...
        </SimpleText>
        <SimpleText color={'#ff0000'}>Não feche esta janela!</SimpleText>

        <SimpleText color={'#898989'}>
          Utilize o aplicativo apenas para tirar dúvidas {'\n'}
          Caso descubra algo incoveniente aja com responsabilidade!
        </SimpleText>
      </PhoneBackground>
    </Container>
  );
};

export default Loading;
