import React, { useState, useCallback } from 'react';
import { useHistory } from 'react-router-dom';

// components
import { Container } from '../../components/ExternalContainer/styles';
import { DDDs } from './constants';
// styles
import {
  PhoneBackground,
  Logo,
  Input,
  InputLabel,
  InputText,
  ButtonWrapper,
  Loader,
  ActionCard,
  AdvicesCard,
  Advice,
  TipsCard,
  TipsTitle,
  Tip,
  ErrorMessage,
} from './styles';

const Home = () => {
  const [phone, setPhone] = useState('');
  const [loading, setLoading] = useState(false);
  const [phoneError, setPhoneError] = useState('');

  const { push } = useHistory();

  const handleInputChange = useCallback(e => {
    const { value } = e.target;

    if (value.length > 11) return;

    setPhoneError('');
    setPhone(value.replace(/[^0-9]/g, ''));
  }, []);

  const handleValidatePhone = useCallback(() => {
    if (phone.length >= 2) {
      const ddd = `${phone[0]}${phone[1]}`;

      if (!DDDs.find(code => code === ddd)) {
        setPhoneError('Por favor insira um DDD válido');
        return false;
      }
    }

    if (phone.length !== 11) {
      setPhoneError('Por favor insira um número válido');
      return false;
    }

    return true;
  }, [phone]);

  const handleButtonClick = useCallback(() => {
    setTimeout(() => setLoading(true), 300);
    setTimeout(() => push('/carregando'), 1300);
  }, [push]);

  return (
    <Container>
      <PhoneBackground>
        <Logo />

        <ActionCard>
          <Input className="Input">
            <InputText
              disabled={loading}
              type="tel"
              pattern="[0-9]"
              id="input"
              className="Input-text"
              maxLength={11}
              placeholder="Insira o número"
              value={phone}
              onBlur={handleValidatePhone}
              onChange={handleInputChange}
            />
            <InputLabel htmlFor="input" className="Input-label">
              Número para espiar
            </InputLabel>
          </Input>

          {phoneError && <ErrorMessage>{phoneError}</ErrorMessage>}

          {!loading ? (
            <ButtonWrapper onClick={handleButtonClick}>
              <button className="button-82-pushable">
                <span className="button-82-shadow"></span>
                <span className="button-82-edge"></span>
                <span className="button-82-front text">ACESSAR AGORA!</span>
              </button>
            </ButtonWrapper>
          ) : (
            <Loader className="lds-default">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </Loader>
          )}
        </ActionCard>

        <AdvicesCard>
          <Advice>
            <p>Não precisa mexer</p>
            <p>no celular da pessoa</p>
          </Advice>

          <Advice>
            <p>Sem notificações</p>
            <p>no celular da pessoa</p>
          </Advice>
        </AdvicesCard>

        <TipsCard>
          <TipsTitle>MUITO UTILIZADO PARA:</TipsTitle>
          <Tip>VlGIAR E CUIDAR DOS FlLHOS</Tip>
          <Tip>CASAlS DESCOBRlR TRAlÇÕES</Tip>
          <Tip>MONlTORAR FUNCIONÁRIOS</Tip>
        </TipsCard>
      </PhoneBackground>
    </Container>
  );
};

export default Home;
